* {
    margin: 0;
    padding: 0;
    user-select: none;
    -webkit-user-select: none;
}

body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-family: Inter;
    color: rgba(86, 82, 82, 1);
    background: rgba(218, 215, 205, 1);
    font-size: 3vh;
    font-weight: 800;
    min-height: 100vh;
    overflow-x: hidden;
    overflow-y: hidden;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1vh;
    background-color: #CACABF;
}

header .PriceBox .coinPriceImage{
    width: 45%;
    height: auto;
}

header .PriceBox span{
    font-family: Inter;
    font-size: 4vh;
    font-weight: 800;
    color: rgba(58, 90, 64, 1);
}


.notification {
    color: rgba(58, 90, 64, 1);
    fill: rgba(58, 90, 64, 1);
    stroke: rgba(58, 90, 64, 1);
    
}
.notification:hover {
    fill: rgba(205, 68, 30, 1);
    stroke: rgba(205, 68, 30, 1);
}

main {
    display: flex;
    flex-direction: column;
    height: 74vh;
    border-bottom-left-radius: 5vh;
    border-bottom-right-radius: 5vh;
    background-color: #CACABF;
}

footer{
    display: flex;
    height: 18vh;
    align-items: center;
}

nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 0.8vh solid rgba(58, 90, 64, 1);
    border-radius: 6vh;
    height: 10vh;
    width: 90%;
    margin: auto;
}
nav button{
    border: none;
    border-radius: 5vh;
    background: rgba(218, 215, 205, 1);
    color: rgba(58, 90, 64, 1);
    fill: rgba(58, 90, 64, 1);
    stroke: rgba(58, 90, 64, 1);
}

nav button:hover {
    fill: rgba(205, 68, 30, 1);
    stroke: rgba(205, 68, 30, 1);
}

nav button.active {
    color: rgba(205, 68, 30, 1);;
    fill: rgba(205, 68, 30, 1);
    stroke: rgba(205, 68, 30, 1);
}

#tree {
    display: flex;
    justify-content: end;
    align-items: end;
    height: 100%;
}

.btnTree{
    background-color: #CACABF;
    border-radius: 5vh;
    border:none;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: end; 
}

.boxWindow {
    display: flex;
    flex-direction: column;
    border: 0.8vh solid rgba(205, 68, 30, 1);
    border-radius: 4vh;
    width: 90%;
    height: 60vh;
    margin: auto;
    margin-top: 3vh;
}

.positionItemBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 4vh;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
    overflow-y: auto;
}

.itemBox {
    display: flex;
    flex-direction: column;
    border-radius: 4vh;
    box-shadow: 0vh 0.3vh 0.3vh 0vh rgba(0, 0, 0, 0.25);
    background: rgba(201, 198, 189, 1);
    width: 18vh;
    height: 21vh;
    padding: 1vh;
    margin: auto;
    margin-top: 2vh;
    margin-bottom: 1vh;
}

.itemBox .PriceBox .coinPriceImage{
    width: 3.5vh;
}

.productName {
    font-size: 3vh;
    margin: auto;
}

.image_item{
    width: 15vh;
    height: auto;
    margin: auto;
}

#page_1 {
    width: 12px;
    height: 12px;
    background: rgba(74, 74, 74, 1);
    width: 50px;
    height: 50px;
    -webkit-border-radius: 25px;
    -moz-border-radius: 25px;
    border-radius: 25px;

}