.notificationBox {
    margin-top: 4vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    max-height: calc(72vh - 10.5vh);
    overflow-y: auto;
    overflow-x: hidden;
  }
  
.notificationBoxItem {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    border: 0.8vh solid rgba(205, 68, 30, 1);
    border-radius: 5vh;
    width: 70%;
    max-width: 100vh; 
    margin: auto;
    margin-bottom: 2vh;
    padding: 5vh 3vh 5vh 3vh;
    text-align: center;
  }

.notificationBoxItem p{
    font-size: 3vh;
}