.switchPanel{
    display: flex;
    justify-content: space-between;
    border: 0.1vh solid rgba(58, 90, 64, 1);
    border-radius: 4vh;
    margin: auto;
    margin-top: 2vh;
    width: 70%;
    height: 4vh;
}

.buttonSwitchPanel {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 4vh;
    width: 50%;
    height: auto;
    background-color: #CACABF;
}

.buttonSwitchPanel span{
    font-size: 2.5vh;
    font-weight: 600;
    color: rgba(58, 90, 64, 1);
}

.buttonSwitchPanel.active {
    border: 0.3vh solid rgba(58, 90, 64, 1);
}