.leaderboardBox{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
}
.leaderboardText{
    margin: auto;
    padding: 2vh;
    font-size: 4.4vh;
}
.leaderboard {
    display: flex;
    flex-direction: column;
    border: 0.8vh solid rgba(205, 68, 30, 1);
    border-radius: 4vh;
    width: 90%;
    height: 56vh;
    margin: auto;
    padding-top: 2vh;
    padding-bottom: 2vh;
}

.player {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 0.25vh solid rgba(205, 68, 30, 1);
    border-radius: 5vh;
    margin-top: 0.4vh;
}
#playerNumber {
    display: flex;
    border: 0.3vh solid rgba(205, 68, 30, 1);
    border-radius: 5vh;
    background-color: rgba(205, 68, 30, 1);;
    color: #ffffff;
    width: 6vh;
    justify-content: center;
}

#playerBalance {
    display: flex;
    border: 0.3vh solid rgba(205, 68, 30, 1);
    border-radius: 5vh;
    background-color: rgba(205, 68, 30, 1);
    color: #ffffff;
    justify-content: center;
    width: 6vh;
}

.table_users{
    width: 100%;
    height: 47.9vh;
    overflow-y: auto;
    padding-top: 0.3vh;
}

#playerNumber{
    font-size: 2.5vh;
}

#playerWalletNumber{
    font-size: 2.5vh;
}

#playerBalance{
    font-size: 2.5vh;
}