.taskBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 2vh;
    box-shadow: 0vh 0.3vh 0.3vh 0vh rgba(0, 0, 0, 0.25);
    background: rgba(201, 198, 189, 1);
    font-size: 3vh;
    width: 90%;
    padding: 1.5vh;
    height: auto; 
    margin: auto;
    margin-top: 2vh;
}

.task_active :hover{
    background-color: rgb(28, 156, 56);
}

.task_not_active{
    background-color: #292626;
}

.tapTask{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    border-radius: 3vh;
    text-align: center;
}

#taskName{
    padding: 0.5vh;
}

#taskText{
    margin-left: 2vh;
    margin-right: 2vh;
    margin-top: -2vh;
}

.tapTask .coinPriceImage{
    width: 100%;
}

.tapTask .PriceText{
    font-size: 3vh;
}

.bottom_panel{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 4vh;
    width: 100%;
    margin: auto;
}

.backIcon{
    display: flex;
    margin-top: 2vh;
    margin-right: 2vh;
    margin-left: auto;
}

.reportButton{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4vh;
    border-color: black;
    width: 60%;
    height: 4vh;
    background-color: #CACABF;
}

.bottom_panel span{
    font-size: 2.5vh;
    color: rgba(86, 82, 82, 1);
}