.wallet_connect {
    padding-top: 20px;
    padding-bottom: 32px;
    position: fixed;
    bottom: 0;
    z-index: 300;
    width: 100%;
    max-height: 100%;
    display: flex;
    flex-direction: column;
    -webkit-overflow-scrolling: touch;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    background-color: rgba(218, 215, 205, 1);
}

._padding-0 {
    padding: 0
}

._direction-column {
    display: flex;
    flex-direction: column
}

._title {
    font-size: 22px;
    font-weight: 590;
    line-height: 28px;
    margin-bottom: 8px;
    text-align: center
}

._description {
    font-size: 17px;
    font-weight: 400;
    line-height: 22px;
    margin-bottom: 24px;
    text-align: center;
}

._link {
    text-align: center;
    margin-bottom: 32px;
    cursor: pointer;
    transition: color .2s ease;
    margin: auto;
}

._link a{
    text-decoration: none;
    user-select: none;
    color: #12af54;
    font-size: 20px;
}

._link a:active {
    color: #04692e;
}

._connect_button {
    padding: 14px 20px;
    text-align: center;
    border-radius: 14px;
    font-size: 20px;
    line-height: 22px;
    font-weight: 590;
    border: 0;
    background: rgba(218, 215, 205, 1);
    color: rgba(58, 90, 64, 1);
}

._connect_button:active {
    transition-duration: 0s;
    background-color: rgba(86, 82, 82, 0.35)
}

.backBlock {
    user-select: none;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 300;
    background-color: rgba(0,0,0,.3);
}
